
import { defineComponent, PropType } from 'vue'
import sideBarController from '@/controllers/sideBarController'
import MenuDataInterface from '@/types/MenuData.interface'

export default defineComponent({
  name: 'SideBar',
  props: {
    jsonData: {
      type: Object as PropType<MenuDataInterface>,
      default: () => ({})
    }
  },
  setup(props) {
    const {
      collapsed,
      collapseSidebar,
      handleClick,
      maxwidth,
      dataWithIcons,
      show,
      mouseEnterHandler,
      mouseLeaveHandler,
      topOfElement,
      selectedElement,
      hoverItemEnter,
      hoverItemLeave,
      showHover,
      iconSwitcher,
      cssVars
    } = sideBarController(props.jsonData!)

    return {
      collapsed,
      collapseSidebar,
      handleClick,
      maxwidth,
      dataWithIcons,
      show,
      mouseEnterHandler,
      mouseLeaveHandler,
      topOfElement,
      selectedElement,
      hoverItemEnter,
      hoverItemLeave,
      showHover,
      iconSwitcher,
      cssVars
    }
  }
})
