
import { defineComponent, PropType } from 'vue'
import sideBarHoverItemController from '@/controllers/sideBarHoverItemController'
import sideBarItemSharedController from '@/controllers/sideBarItemSharedController'
import MenuDataInterface from '@/types/MenuData.interface'

export default defineComponent({
  name: 'SideBarHoverItem',
  emits: ['hover-item-enter', 'hover-item-leave'],
  props: {
    topOfElement: {
      type: Number
    },
    collapsed: {
      type: Boolean,
      default: true
    },
    caption: {
      type: String
    },
    selectedElement: {
      type: String
    },
    level: {
      type: Number,
      default: 1
    },
    show: {
      type: Boolean,
      default: true
    },
    jsonData: {
      type: Object as PropType<MenuDataInterface>,
      default: () => ({})
    }
  },
  setup(props, { emit }) {
    const {
      selectedElementValue,
      // hoverEnter,
      hoverItemEnter,
      hoverItemLeave,
      childData,
      styleGenerator
    } = sideBarHoverItemController(emit, props)

    const {
      myChildren,
      toggleArrow,
      arrowClass,
      isFirstLevel,
      hasChildren
    } = sideBarItemSharedController(props.level, selectedElementValue.value[0])

    return {
      hoverItemEnter,
      hoverItemLeave,
      selectedElementValue,
      isFirstLevel,
      hasChildren,
      arrowClass,
      toggleArrow,
      myChildren,
      childData,
      styleGenerator
    }
  }
})
