
import { defineComponent, PropType } from 'vue'
import MenuDataInterface from '@/types/MenuData.interface'
import MenuItemsInterface from '@/types/MenuItems.interface'
import sideBarItemController from '@/controllers/sideBarItemController'
import sideBarItemSharedController from '@/controllers/sideBarItemSharedController'

export default defineComponent({
  name: 'SideBarItem',
  emits: ['mouseenter', 'mouseleave'],
  props: {
    collapsed: {
      type: Boolean,
      default: true
    },
    caption: {
      type: String
    },
    icon: {
      type: String
    },
    show: {
      type: Boolean,
      default: true
    },
    children: {
      type: Array as PropType<MenuDataInterface>,
      default: () => []
    },
    item: {
      type: Object as PropType<MenuItemsInterface>,
      default: () => ({})
    },
    level: {
      type: Number,
      default: 1
    },
    indexCounter: {
      type: String
    },
    isHoverItem: {
      type: Boolean,
      default: false
    },
    topOfElement: {
      type: Number
    },
    showSideBarHoverItem: {
      type: Boolean,
      default: false
    },
    pkMenuItem: {
      type: String
    },
    isChildItem: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const {
      arrowToggle,
      myChildren,
      mouseEnterHandler,
      mouseLeaveHandler,
      childIsUnknowm
    } = sideBarItemController(emit, props)

    const {
      toggleArrow,
      arrowClass,
      isFirstLevel,
      hasChildren
    } = sideBarItemSharedController(props.level, props!.item)

    return {
      toggleArrow,
      arrowToggle,
      arrowClass,
      myChildren,
      hasChildren,
      isFirstLevel,
      mouseEnterHandler,
      mouseLeaveHandler,
      childIsUnknowm
    }
  }
})
